.form__submit-btn {
	padding: 14px 0;
	border: 0;
	border-radius: 2px;
	color: #fff;
	background-color: #000;
	font-weight: 400;
	font-size: 18px;
	line-height: 22px;
	margin-top: -30px;
	transition: opacity 0.2s ease-out;
}

.form__submit-btn:hover {
	opacity: 0.8;
	cursor: pointer;
}

@media screen and (max-width: 930px) {
	.form__submit-btn {
		font-size: 14px;
		line-height: 17px;
	}
}