.popup {
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	display: flex;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, .5);
	align-items: center;
	justify-content: center;
	transition: visibility 0s, opacity .2s ease-in;
  }